.home-container {
    width: 100%;
    height: 100%;
    display: block;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
    background-color: #f5f9f6;
    color: #164e98;

    .head-content {
        display: flex;
        position: relative;
        flex-direction: row;
        align-items: center;
        justify-content: flex-start;
        height: 350px;
        width: 100%;
        background-color: #164e98;
        color: white;

        .profile-image {
            position: relative;
            left: 25px;
            display: flex;
            height: 300px;
            width: 300px;
            border-radius: 50%;
            border: solid 8px rgba(168, 167, 167, 0.714);
            background-image: url('../images/dog_profile.jpg');
            background-size: cover;
        }

        .head-info {
            height: 100%;
            position: relative;
            left: 25px;
            width: calc(100% - 342px);
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            h1 {
                font-size: 100px;
                font-weight: bold;
                margin: 0;
            }

            h3 {
                font-size: 36px;
                margin: 10px;
            }
        }
    }

    .icon-container {
        position: relative;
        display: flex;
        width: 100%;
        // height: 100px;
        margin-top: 5px;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;


        div {
            height: 100px;
            width: 100px;;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            margin: 0 40px;
            font-weight: bold;
            font-size: 16px;

            i {
                font-size: 64px;
                font-weight: normal;
            }
        }
    }

    .home-content {
        // display: flex;
        flex-direction: column;
        align-items: center;
        position: relative;
        justify-content: center;
        text-align: left;
        width: 100%;
        min-height: calc(100% - 505px);
        font-size: 2.5vh;

        p {
            display: flex;
            margin: 35px 50px;
        }

        .last-p {
            margin-bottom: 10px;
        }
    }

    @media(max-width: 1024px) {
        
        .head-content{
            flex-direction: column;
            height: 450px;

            .profile-image {
                left: 0;
                top: 25px;
                width: 200px;
                height: 200px;;
            }

            .head-info {
                left: 0;
                width: 95%;
                height: 250px;;
                h1 {
                    font-size: 36px;
                }

                h3 {
                    font-size: 24px;
                }
            }
        }

        .icon-container {

            div {
                margin: 5px;
            }
        }
    }
}